/* * General: Miscellaneous * ---------------------- */a.text-muted:hover {  color: theme-color(primary) !important;}.border-transparent {  border-color: transparent !important;}// Description Blocks.description-block {  display: block;  margin: 10px 0;  text-align: center;  &.margin-bottom {    margin-bottom: 25px;  }  > .description-header {    margin: 0;    padding: 0;    font-weight: 600;    font-size: 16px;  }  > .description-text {    text-transform: uppercase;  }}// Background colors@each $name, $color in $theme-colors {  .bg-#{$name} {    &,    a {      color: color-yiq($color) !important;    }  }}.bg-gray {  color: #000;  background-color: $gray-500;}.bg-gray-light {  background-color: lighten($gray-200, 3%);  color: color-yiq(lighten($gray-200, 3%)) !important;}.bg-black {  background-color: $black;  color: color-yiq($black) !important;}.bg-white {  background-color: $white;  color: color-yiq($white) !important;}// Disabled![class^="bg-"].disabled {  opacity: .65;}.link-muted {  color: darken($gray-500, 30%);  &:hover,  &:focus {    color: darken($gray-500, 40%);  }}.link-black {  color: #666;  &:hover,  &:focus {    color: #999;  }}// Hide elements by display none only.hide {  display: none !important;}// Remove borders.no-border {  border: 0 !important;}// Remove box shadow.no-shadow {  box-shadow: none !important;}// Unstyled List.list-unstyled {  list-style: none;  margin: 0;  padding: 0;}.list-group-unbordered {  > .list-group-item {    border-left: 0;    border-right: 0;    border-radius: 0;    padding-left: 0;    padding-right: 0;  }}// Remove border radius.flat {  @include border-radius(0 !important);}// _fix for sparkline tooltip.jqstooltip {  padding: 5px !important;  width: auto !important;  height: auto !important;}// Gradient Background colors@each $name, $color in $theme-colors {  .bg-#{$name}-gradient {    @include gradient($color, $color, lighten($color, 10%));    color: color-yiq($color);  }}// Description Block Extension.description-block {  .description-icon {    font-size: 16px;  }}// List utility classes.list-header {  font-size: 15px;  padding: 10px 4px;  font-weight: bold;  color: #666;}.list-seperator {  height: 1px;  background: $card-border-color;  margin: 15px 0 9px 0;}.list-link {  > a {    padding: 4px;    color: #777;    &:hover {      color: #222;    }  }}// User block.user-block {  @include clearfix();  img {    width: 40px;    height: 40px;    float: left;  }  .username,  .description,  .comment {    display: block;    margin-left: 50px;  }  .username {    font-size: 16px;    font-weight: 600;  }  .description {    color: #999;    font-size: 13px;  }  &.user-block-sm {    img {      @extend .img-sm;    }    .username,    .description,    .comment {      margin-left: 40px;    }    .username {      font-size: 14px;    }  }}// Image sizes.img-sm,.img-md,.img-lg {  float: left;}.img-sm {  width: 30px !important;  height: 30px !important;  + .img-push {    margin-left: 40px;  }}.img-md {  width: 60px;  height: 60px;  + .img-push {    margin-left: 70px;  }}.img-lg {  width: 100px;  height: 100px;  + .img-push {    margin-left: 110px;  }}// Image bordered.img-bordered {  border: 3px solid $gray-500;  padding: 3px;}.img-bordered-sm {  border: 2px solid $gray-500;  padding: 2px;}// Rounded and Circle Images.img-rounded {  @include border-radius($border-radius)}.img-circle {  @include border-radius(50%);}// Image sizes.img-size-64,.img-size-50,.img-size-32 {  height: auto;}.img-size-64 {  width: 64px;}.img-size-50 {  width: 50px;}.img-size-32 {  width: 32px;}// Block sizes.size-32,.size-40,.size-50 {  display: block;  text-align: center;}.size-32 {  width: 32px;  height: 32px;  line-height: 32px;}.size-40 {  width: 40px;  height: 40px;  line-height: 40px;}.size-50 {  width: 50px;  height: 50px;  line-height: 50px;}// General attachemnt block.attachment-block {  border: 1px solid $card-border-color;  padding: 5px;  margin-bottom: 10px;  background: #f7f7f7;  .attachment-img {    max-width: 100px;    max-height: 100px;    height: auto;    float: left;  }  .attachment-pushed {    margin-left: 110px;  }  .attachment-heading {    margin: 0;  }  .attachment-text {    color: #555;  }}.connectedSortable {  min-height: 100px;}.ui-helper-hidden-accessible {  border: 0;  clip: rect(0 0 0 0);  height: 1px;  margin: -1px;  overflow: hidden;  padding: 0;  position: absolute;  width: 1px;}.sort-highlight {  background: #f4f4f4;  border: 1px dashed #ddd;  margin-bottom: 10px;}// Charts.chart {  position: relative;  overflow: hidden;}.flex-1 {  flex: 1;}