/* * Page: 400 and 500 error pages * ------------------------------ */.error-page {  width: 600px;  margin: 20px auto 0 auto;  @include media-breakpoint-down(sm) {    width: 100%;  }  //For the error number e.g: 404  > .headline {    float: left;    font-size: 100px;    font-weight: 300;    @include media-breakpoint-down(sm) {      float: none;      text-align: center;    }  }  //For the message  > .error-content {    margin-left: 190px;    @include media-breakpoint-down(sm) {      margin-left: 0;    }    > h3 {      font-weight: 300;      font-size: 25px;      @include media-breakpoint-down(sm) {        text-align: center;      }    }    display: block;  }}