/* * Component: Timeline * ------------------- */.timeline {  position: relative;  margin: 0 0 30px 0;  padding: 0;  list-style: none;  // The line  &:before {    content: '';    position: absolute;    top: 0;    bottom: 0;    width: 4px;    background: #ddd;    left: 31px;    margin: 0;    @include border-radius($border-radius);  }  > li {    position: relative;    margin-right: 10px;    margin-bottom: 15px;    @include clearfix();    // The content    > .timeline-item {      @include box-shadow($card-shadow);      @include border-radius($border-radius);      margin-top: 0;      background: $white;      color: #444;      margin-left: 60px;      margin-right: 15px;      padding: 0;      position: relative;      // The time and header      > .time {        color: #999;        float: right;        padding: 10px;        font-size: 12px;      }      > .timeline-header {        margin: 0;        color: #555;        border-bottom: 1px solid $card-border-color;        padding: 10px;        font-size: 16px;        line-height: 1.1;        > a {          font-weight: 600;        }      }      // Item body and footer      > .timeline-body, > .timeline-footer {        padding: 10px;      }    }    // The icons    > .fa,    > .glyphicon,    > .ion {      width: 30px;      height: 30px;      font-size: 15px;      line-height: 30px;      position: absolute;      background: $gray-500;      border-radius: 50%;      text-align: center;      left: 18px;      top: 0;    }  }  // Time label  > .time-label {    > span {      font-weight: 600;      padding: 5px;      display: inline-block;      background-color: $white;      @include border-radius(4px);    }  }}.timeline-inverse {  > li {    > .timeline-item {      background: $gray-100;      border: 1px solid #ddd;      @include box-shadow(none);      > .timeline-header {        border-bottom-color: #ddd;      }    }  }}